<template>
  <div class="film" v-if="film.data">
      {{film.data[0].title}}
      <iframe class="film__video" :src="film.data[0].iframe_src" frameborder="0" allowfullscreen></iframe>
      <div class="genre">Жанры: {{moreFilm.Genre}}</div>
      <div class="description">
        <h3>Описание:</h3>
        <div class="description__text">
          {{moreFilm.Plot}}
        </div>
      </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'Home',
  data() {
    return {
      moreFilm: '',
    }
  },
  computed: {
    ...mapGetters(['film'])
  },
  methods: {
    ...mapActions(['getFilm'])
  },
  async mounted() {
    await this.getFilm(this.$route.params.id)
    let res = await fetch(`https://www.omdbapi.com/?apikey=d36d7f24&i=${this.film.data[0].imdb_id}`)
    console.log('r',res)
    this.moreFilm = await res.json()
    console.log('more',this.moreFilm)
  },
}
</script>
<style>
    .film {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .film__video {
      width: 50vw;
      height: 28vw;
    }
</style>